@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&family=Rubik:ital,wght@0,400;0,500;1,400&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
header{z-index:1000;background-color:#fff;-webkit-filter:drop-shadow(0px 1px 3px rgba(0,0,0,0.1));filter:drop-shadow(0px 1px 3px rgba(0,0,0,0.1));display:flex;align-items:center;padding:10px 15px;justify-content:space-between;position:fixed;width:calc(100% - 30px);top:0}.header-group{display:flex;align-items:center}.header-group a{line-height:0}.header-group .menu-links{display:flex;align-items:center}.header-group .menu-links a{font-family:'Rubik', sans-serif;font-weight:400;text-decoration:none;color:#232528;transition:all ease-in-out 300ms;font-size:14px;line-height:20px}.header-group .menu-links a:hover{color:#2D3639}.header-group .menu-links a:not(:last-of-type){margin-right:30px}.header-group.left>a{margin-right:24px}.header-group.right>*:not(:first-of-type){margin-left:14px}

.row{display:flex;justify-content:space-between}.row .info-label-left{display:flex;align-items:center}.row .info-label-left p{font-style:normal;font-size:14px;line-height:20px;color:#BDBDBD;font-weight:500;font-family:'Inter', sans-serif}.row .info-label-left p.label{font-weight:600;color:#828282;margin-right:6px}.row .info-label-right{display:flex;align-items:center}.row .info-label-right img{height:14px;margin-right:12px}.row .info-label-right p{font-style:normal;font-size:12px;line-height:20px;color:#BDBDBD;font-weight:400;font-family:'Inter', sans-serif}.row .info-label-right p.label{color:#232528;margin-right:8px}

.cv-wrapper{background:#FFFFFF;box-shadow:0px 2px 6px rgba(0,0,0,0.1);margin-top:30px;min-height:1329px;padding:60px;position:relative}.cv-wrapper .cv-header{padding:15px;justify-content:space-between;display:flex;flex-wrap:nowrap}.cv-wrapper .cv-header .left-col{width:67%;min-height:140px;padding-right:15px}.cv-wrapper .cv-header .left-col .icon-inputs{display:flex;margin-top:20px;flex-flow:row wrap;align-items:center}.cv-wrapper .cv-header .left-col .icon-inputs>div{width:50%}.cv-wrapper .cv-content{display:flex;margin-top:10px}.cv-wrapper .cv-content .experience{width:58%}.section-heading{font-family:'Rubik', sans-serif;font-style:normal;font-weight:500;font-size:28px;line-height:33px;color:#2D3639;border-bottom:3px solid #2D3639;margin:15px 15px 0 15px}.tinted-background{position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.2)}

.input-item{border:none;outline:none;width:100%;padding:0}.input-item.name{font-family:'Rubik', sans-serif;font-style:normal;font-weight:500;font-size:42px;line-height:50px;color:#2D3639;text-transform:uppercase}.input-item.name::-webkit-input-placeholder{color:#2D3639;opacity:50%}.input-item.name:-ms-input-placeholder{color:#2D3639;opacity:50%}.input-item.name::placeholder{color:#2D3639;opacity:50%}.input-item.role{font-family:'Rubik', sans-serif;font-style:normal;font-weight:500;font-size:21px;line-height:25px;color:#3F9CDC;text-transform:capitalize}.input-item.role::-webkit-input-placeholder{color:#3f9ddc;opacity:50%}.input-item.role:-ms-input-placeholder{color:#3f9ddc;opacity:50%}.input-item.role::placeholder{color:#3f9ddc;opacity:50%}.input-item.contact{font-family:'Inter', sans-serif;font-style:normal;font-weight:bold;font-size:12px;line-height:20px;color:#384347}.input-item.contact::-webkit-input-placeholder{color:#384347;opacity:50%}.input-item.contact:-ms-input-placeholder{color:#384347;opacity:50%}.input-item.contact::placeholder{color:#384347;opacity:50%}.input-item.job-title{font-family:'Rubik', sans-serif;font-style:normal;font-weight:normal;font-size:22px;line-height:26px;color:#2D3639}.input-item.job-title::-webkit-input-placeholder{color:#2D3639;opacity:50%}.input-item.job-title:-ms-input-placeholder{color:#2D3639;opacity:50%}.input-item.job-title::placeholder{color:#2D3639;opacity:50%}.input-item.company-name{margin-top:4px;color:#3F9CDC;font-style:normal;font-weight:500;font-size:16px;line-height:22px;font-family:'Rubik', sans-serif}.input-item.company-name::-webkit-input-placeholder{color:#3F9CDC;opacity:50%}.input-item.company-name:-ms-input-placeholder{color:#3F9CDC;opacity:50%}.input-item.company-name::placeholder{color:#3F9CDC;opacity:50%}.input-item.job-details{font-family:'Inter', sans-serif;color:#384347;font-style:normal;font-weight:500;font-size:12px;line-height:20px}.input-item.job-details::-webkit-input-placeholder{color:#384347;opacity:50%}.input-item.job-details:-ms-input-placeholder{color:#384347;opacity:50%}.input-item.job-details::placeholder{color:#384347;opacity:50%}.input-icons-row{display:flex;padding-bottom:4px;padding-right:15px;flex-wrap:nowrap;box-sizing:border-box}.input-icons-row img{margin-right:4px;opacity:50%;transition:all ease-in-out 300ms}.input-icons-row img.solid{opacity:100%}.input-icons-row.job-det{padding-right:10px}

.item{background-color:#fff;padding:15px;border:1px solid #fff;transition:all ease-in-out 300ms;box-sizing:border-box;border-radius:9px}.item:hover{border:1px solid #2DC08D}.item .job-details-row{display:flex;margin-top:3px}.item.focused{transition:all ease-in-out 300ms;z-index:100;position:relative;border:1px solid #2DC08D}.item .text-decoration{position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);top:0;left:50%;display:flex;border-radius:30px;box-shadow:0px 4px 30px rgba(0,0,0,0.1);align-items:center;background-color:white;padding:15px}.item .text-decoration button{background-color:transparent;border:none}.item .add-remove{position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);top:0;left:50%;display:flex;border-radius:30px;box-shadow:0px 4px 30px rgba(0,0,0,0.1);align-items:center}.item .add-remove .add{padding:10px;cursor:pointer;display:flex;background-color:#2DC08D;border:none;align-items:center;border-radius:30px 0 0 30px;border-right:1px solid #E0E0E0}.item .add-remove .add img{margin-right:8px}.item .add-remove .add p{font-style:normal;font-weight:normal;font-size:15px;line-height:18px;font-family:'Inter', sans-serif;color:#FFFFFF}.item .add-remove .remove{cursor:pointer;padding:10px;border:none;align-items:center;border-radius:0 30px 30px 0;background-color:#FFFFFF;display:flex}

.divtext{overflow:auto;width:100%;min-height:20px;margin-top:4px;display:block;border:none;resize:none;outline:none;color:#384347;font-style:normal;font-weight:500;font-size:13px;line-height:20px;font-family:'Inter', sans-serif}.divtext b{font-weight:bold}.divtext u{text-decoration:underline}.divtext i{font-style:italic}#secretDiv{position:absolute;width:100%;left:0;margin:0;top:-4px;pointer-events:none;padding:0;visibility:hidden}[contenteditable][placeholder]:empty:before{content:attr(placeholder);position:absolute;font-size:13px;line-height:20px;color:#384347;opacity:0.5;background-color:transparent}.wrapper-mistakes{box-sizing:content-box;pointer-events:none;overflow:hidden;border:0px none;border-radius:0px;padding:0px;margin:0px}

.mistake{position:absolute;transition:all ease-in-out 300ms}.mistake.hov{background-color:rgba(235,87,87,0.66)}.mistake.whole{background-color:rgba(235,87,87,0.33)}

.pop-over{position:absolute;width:290px;background:white;padding:12px 16px;border-radius:6px;-webkit-filter:drop-shadow(0px 2px 20px rgba(0,0,0,0.2));filter:drop-shadow(0px 2px 20px rgba(0,0,0,0.2));top:calc(100% + 9px);z-index:10}.pop-over::before{z-index:10;content:"";position:absolute;margin-left:0;bottom:0;width:20px;height:20px;box-sizing:border-box;border-radius:0 4px;background:#fff;border-color:#0000 #0000 #f1f1f2 #f1f1f2;border-style:solid;border-width:1px;-webkit-transform-origin:0 0;transform-origin:0 0;top:0px;left:20%;-webkit-transform:rotate(144deg) skew(20deg);transform:rotate(144deg) skew(20deg)}.pop-over p.content{margin-top:12px;font-style:normal;font-weight:normal;font-size:12px;font-family:'Inter', sans-serif;line-height:20px;color:#232528}.pop-over .heading{display:flex;justify-content:space-between;align-items:center}.pop-over .heading>div{display:flex;align-items:center}.pop-over .heading>div img{margin-right:8px}.pop-over .heading>div h3{color:#232528;font-family:'Inter', sans-serif;font-style:normal;font-weight:bold;font-size:12px;line-height:20px}.pop-over .heading>div .checkbox{box-sizing:border-box;width:16px;height:16px;display:inline-block;margin-right:10px;border:1px solid #BDBDBD;box-sizing:border-box;border-radius:3px}.pop-over .heading>div .ignore{font-family:'Rubik', sans-serif;color:#232528;font-style:normal;font-weight:normal;font-size:12px;line-height:20px}

